h1,
h2 {
  font-size: 1.1rem;
  font-weight: normal;
  align-self: flex-start;
  margin: .1rem .25rem;
}

.year {
  font-weight: 200;
}

.year--glow {
  color: rgb(208, 89, 255);
  text-shadow: 1px 1px 4px #ee00d2;
}

.name {
  color: rgb(70, 70, 70);
}

:root {
  --image-width: clamp(150px, 45vw, 300px);
}

.tea {
  width: calc(1rem + var(--image-width));
  padding: .5rem .1rem;
  margin: 1.2rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  position: relative;
}

.tea:before {
  --size: 8px;
  --color: rgb(222,222,222);
  content: ' ';
  display: block;
  border-bottom: 1px solid  var(--color);
  border-right: 1px solid var(--color);
  position: absolute;
  top: calc(-.5 * var(--size));
  left: calc(-.75 * var(--size));
  height: var(--size);
  width: var(--size);
}
